<template>
  <div class="_page">
    <Header>
      <h1 slot="title">Ëá™ÈÄÇÂ∫îËÆ°ÁÆóÂô®</h1>
    </Header>
    <div class="computed flex-xb">
      <div class="ev source">
        <div class="head flex-yc flex-xb">
          <p>ÈúÄË¶ÅËÆ°ÁÆóÁöÑÂÜÖÂÆπ</p>

          <el-button
            size="small"
            type="primary"
            icon="el-icon-setting"
            @click="switchConfig"
          >
            {{ hidenConfig ? 'OK' : 'ËÆæÁΩÆËÆ°ÁÆóËßÑÂàô' }}
          </el-button>
        </div>
        <textarea
          v-show="!hidenConfig"
          v-model="oldStyle"
        ></textarea>
        <div
          class="config"
          v-show="hidenConfig"
        >
          <el-form
            ref="form"
            :model="config"
            label-width="10em"
          >
            <el-form-item label="ÊòØÂê¶ËÆ°ÁÆóËæπÊ°Ü">
              <el-switch v-model="config.isComputedBorder"> </el-switch>
              <span style="margin-left: 20px">
                <el-tag
                  v-if="config.isComputedBorder"
                  type="info"
                  >ËÆ°ÁÆó</el-tag
                >
                <el-tag v-else>‰∏çËÆ°ÁÆó</el-tag>
              </span>
            </el-form-item>
            <el-form-item label="ÁΩëÁ´ôÊ®°Âºè">
              <el-switch v-model="config.isSiteMode"></el-switch>
              <span style="margin-left: 20px">
                <el-tag
                  v-if="config.isSiteMode"
                  type="info"
                  >‰ºöËá™Âä®Âä†mediaÊü•ËØ¢</el-tag
                >
                <el-tag v-else>‰∏ç‰ºöËá™Âä®Âä†mediaÊü•ËØ¢</el-tag>
              </span>
            </el-form-item>
            <el-form-item
              label="‰æøÊç∑ÁΩëÁ´ôÊ®°Âºè"
              v-show="config.isSiteMode"
            >
              <div class="input-tip">ÁΩëÁ´ôÊ®°ÂºèÊó∂ÊâãÊú∫Á´ØËá™Âä®Âà†Èô§Â§ö‰Ωôcss</div>
              <el-switch v-model="config.removeMobCss"></el-switch>
              <span style="margin-left: 20px">
                <el-tag
                  v-if="config.removeMobCss"
                  type="info"
                  >‰ºöËá™Âä®Âä†mediaÊü•ËØ¢</el-tag
                >
                <el-tag v-else>‰∏ç‰ºöËá™Âä®Âä†mediaÊü•ËØ¢</el-tag>
              </span>
            </el-form-item>
            <el-form-item label="ÊñáÂ≠óÊúÄÂ∞èÂÄº">
              <div class="input-tip">Â∞è‰∫éÁ≠â‰∫éËøô‰∏™ÂÄºÁöÑÊñáÂ≠ó‰∏çÂèÇ‰∏éËÆ°ÁÆó</div>
              <el-input v-model="config.sizeMin"> </el-input>
            </el-form-item>
            <el-form-item label="ËÆ°ÁÆóÊñπÊ≥ï=Value √∑">
              <div class="input-tip">1920/375</div>
              <el-input v-model.trim="config.sizeComputed"> </el-input>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="ev result">
        <div class="head flex-yc flex-xb">
          <p>ËÆ°ÁÆóÁªìÊûú:</p>

          <el-button
            size="small"
            type="primary"
            icon="el-icon-document-copy"
            @click="copyTextToClipboard($event)"
          >
            ÁÇπÂáªÂ§çÂà∂
          </el-button>
        </div>
        <pre>
            <code id="Code" class="language-css" v-html="newStyle"></code>
          </pre>
      </div>
    </div>

    <div :class="`tips ${tipShow ? 'show' : ''}`">‚úÖ Copy successfully !!! üåà</div>
  </div>
</template>

<script>
import '@/assets/css/arta.min.css';
import '@/assets/css/computed-elder.css';
import Header from '@/components/Header.vue';
import hljs from '@/assets/js/highlight.min.js';
export default {
  name: 'computedMedia',
  components: { Header },
  data() {
    return {
      oldStyle: `.home1 {
    border-bottom: 1px solid #ECEEF2;
    padding-bottom: 7px;
}
.home1 .top,
.home1 .top .asdf{
    margin: 12px 0 9px;
    font-size: 14px;
}`,
      hidenConfig: false,
      tipShow: false,
      newStyle: '',
      config: {
        sizeComputed: 1920,
        sizeMin: 1,
        isComputedBorder: false,
        isSiteMode: true, //ÁΩëÁ´ôÊ®°Âºè-‰ºöËá™Âä®Âä†mediaÊü•ËØ¢
        removeMobCss: true,
      },
    };
  },
  watch: {
    oldStyle: {
      handler: function (_new, _old) {
        this.createdNewStyle();
      },
      immediate: true,
    },
  },
  methods: {
    switchConfig() {
      this.hidenConfig = !this.hidenConfig;
      this.createdNewStyle();
    },
    createdNewStyle() {
      let styleList = this.oldStyle;
      // styleList = styleList.replaceAll("}", "}@@@");
      styleList = styleList.replace(/}/g, '}@@@');
      styleList = styleList.split('@@@');

      let allStyle = this.computedStyle(styleList);

      //ÁΩëÁ´ôÊ®°Âºè
      if (this.config.isSiteMode) {
        var mobStyle = '';
        if (this.config.removeMobCss) {
          mobStyle = this.computedMobStyle(styleList);
        }
        allStyle = `/* pc */
@media screen {
${allStyle}
}
/* mob */
@media screen and (max-width:991px) {
${mobStyle}
}
/* Ëá™Âä®ËÆ°ÁÆóÂâç */
@media screen and (max-width:1px) {
${this.oldStyle}
}`;
      }

      this.newStyle = allStyle;
      this.$nextTick(() => {
        let el = document.getElementById('Code');
        hljs.highlightElement(el);
      });
    },
    /**
     * ËÆ°ÁÆópx=>vw
     * ËøîÂõûËÆ°ÁÆóÂêéÁöÑÊ†∑Âºè
     */
    computedStyle(styleList) {
      let allStyle = '';
      styleList.forEach((styleEv) => {
        // Âä†‰∏äÂâçÁºÄ
        // styleEv = styleEv.replace(/.*(-,|{)/g, ($1) => {
        //   // ÂøΩÁï•Â™í‰ΩìÊü•ËØ¢
        //   if ($1.indexOf('@media') != -1) {
        //     return $1;
        //   }
        //   // ÂøΩÁï•Âä®Áîª
        //   if ($1.indexOf('@keyframes') != -1) {
        //     return $1;
        //   }
        //   // ÂøΩÁï•Âä®ÁîªÂÜÖÈÉ®ÁöÑfromÔºåto,0%,25%...
        //   if (/^[from|to|\d]/g.test($1.trim())) {
        //     return $1;
        //   }
        //   // console.log($1);
        //   let res = $1.trim();
        //   let trimLength = $1.length - res.length;
        //   let trimStr = '';
        //   for (var i = 0; i < trimLength; i++) {
        //     trimStr += ' ';
        //   }
        //   return trimStr + res;
        // });
        styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
          // ÊãÜÂàÜÔºåËé∑ÂèñÂà∞ÊØè‰∏™Â±ûÊÄß
          // let arr = $1.replaceAll(";", ";@@@").split("@@@");
          let arr = $1.replace(/;/g, ';@@@').split('@@@');
          let str = '';
          arr.forEach((line, i) => {
            if (line.indexOf('font-size') != -1) {
              str += computedPX(line, this.config.sizeComputed, this.config.sizeMin);
            } else if (line.indexOf('border') != -1) {
              if (!this.config.isComputedBorder) {
                str += line;
                return;
              }
              str += computedPX(line, this.config.sizeComputed);
            } else {
              str += computedPX(line, this.config.sizeComputed);
            }
          });
          return str;
        });
        allStyle = allStyle + styleEv;
      });

      return allStyle;
      /*
       * line Ë¶ÅÊü•ÊâæÁöÑÁõÆÊ†áÔºå‰æãÂ¶ÇÔºömargin: 12px 0 9px;
       * formula ËÆ°ÁÆóÁöÑÊï∞ÂÄºÔºå‰æãÂ¶Ç 1920
       * minSize ÊúÄÂ∞èÂÄºÔºåÂ¶ÇÊûúÂ∞è‰∫éÊúÄÂ∞èÂÄºÂàô‰ΩøÁî®ÊúÄÂ∞èÂÄº
       */
      function computedPX(line, formula, minSize) {
        if (minSize) {
          return line.replace(/(\d+px)/g, ($1) => {
            if (parseFloat($1) < minSize) {
              return minSize + 'px';
            }
            return Math.round(((parseFloat($1) / formula) * 100000).toFixed(4)) / 1000 + 'vw';
          });
        }
        return line.replace(/(\d+px)/g, ($1) => {
          return Math.round(((parseFloat($1) / formula) * 100000).toFixed(4)) / 1000 + 'vw';
        });
      }
    },
    /**
     * ËÆ°ÁÆóÊâãÊú∫Á´ØÊ†∑ÂºèÔºåÂà†Èô§Â§ö‰Ωôcss
     * ËøîÂõûËÆ°ÁÆóÂêéÁöÑÊ†∑Âºè
     */
    computedMobStyle(styleList) {
      let mobStyle = '';
      styleList.forEach((styleEv) => {
        // console.log(styleEv);
        styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
          // ÊãÜÂàÜÔºåËé∑ÂèñÂà∞ÊØè‰∏™Â±ûÊÄß
          let arr = $1.replace(/;/g, ';@@@').split('@@@');
          let str = '';
          arr.forEach((line, i) => {
            if (
              line.indexOf('px') != -1 ||
              line.indexOf('%') != -1 ||
              line.indexOf('{') != -1 ||
              line.indexOf('}') != -1 ||
              line.indexOf('flex') != -1
            ) {
              str += line;
            }
          });
          return str;
        });

        mobStyle = mobStyle + styleEv;
      });
      return mobStyle;
    },

    copyTextToClipboard(event) {
      let that = this;
      this.tipShow = false;
      let text = this.newStyle;
      if (!navigator.clipboard) {
        function fallbackCopyTextToClipboard(text) {
          // 1.ÂàõÂª∫‰∏Ä‰∏™ÂèØÈÄâ‰∏≠ÂÖÉÁ¥†
          let textArea = document.createElement('textarea');
          textArea.value = text;
          // 2.‰ΩøÁî®ÂÆö‰ΩçÔºåÈòªÊ≠¢È°µÈù¢ÊªöÂä®
          textArea.style.top = '0';
          textArea.style.left = '0';
          textArea.style.position = 'fixed';
          document.body.appendChild(textArea);
          textArea.focus();
          textArea.select();
          try {
            var successful = document.execCommand('copy');
            var msg = successful ? 'successful' : 'unsuccessful';
            // console.log('Fallback: Copying text command was ' + msg);
            that.tipShow = true;
            var iii = setTimeout(() => {
              that.tipShow = false;
              clearTimeout(iii);
            }, 1000);
          } catch (err) {
            console.error('Fallback: Oops, unable to copy', err);
          }
          // 3.ÁßªÈô§ÂÖÉÁ¥†
          document.body.removeChild(textArea);
        }
        fallbackCopyTextToClipboard(text);
        return;
      }
      navigator.clipboard.writeText(text).then(
        function () {
          // console.log('Async: Copying to clipboard was successful!');
          that.tipShow = true;
        },
        function (err) {
          console.error('Async: Could not copy text: ', err);
        }
      );
    },
  },
};
</script>
